import { configureStore } from '@reduxjs/toolkit'
import applicantSlice from './applicantSlice'
import helperSlice from './helperSlice'
import ssoSlice from './ssoSlice'
import stepSlice from './stepSlice'
import themeSlice from './themeSlice'
import { loadState, saveState } from './utils/admissionUtil'

const state = loadState();
const config = {
  reducer : {
    helper : helperSlice,
    applicant : applicantSlice,
    step : stepSlice,
    sso : ssoSlice,
    theme: themeSlice,
  } 
}
if(state) config.preloadedState = state.state;

const store = configureStore(config)
store.subscribe(() => saveState({ state: store.getState() }));

export default store;
